@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    display: flex;
    flex-direction: column;
    /* background: linear-gradient(to bottom right, #2F5BFE, pink); */
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
    
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}


.row{
    display: flex;
    flex-flow: column;
    gap: 2rem;

     @include m.tablet {
        flex-flow: row;
    } 
} 







