
body {
    font-family: "Roboto", sans-serif;
    margin: 0;
    
}

h2 {
    color: #2c2c51;
    margin: 40px 0;
}

.icon {
    width: 150px;
    height: auto;
}

.card {
    border: 1px solid #d8d8d8;
    border-radius: 4px;
    min-height: auto;

    h3 {
        text-align: center;
        padding-top: 24px;
        padding-bottom: 8px;
    }

    .card-content {
        display: flex;
        justify-content: center;
    }

    .gaugeMeter {
        height: 280px;
        position: relative;
    }

    u {
        text-decoration: none;
    }

    span {
        display: none;
    }

   p {
        width: 100%;
        height: min-content;
        font-size: 3rem;
        line-height: 0;
        margin: auto;
        text-align: center;
        margin-bottom: 1.5rem;
    }
}